/* Hide scrollbar for Chrome, Safari, and Opera */
::-webkit-scrollbar {
  display: none;
}

/* Hide scrollbar for Firefox */
html {
  scrollbar-width: none;
  overflow: -moz-scrollbars-none;
}

/* Hide scrollbar for IE and Edge */
 body {
  -ms-overflow-style: none;  
}

@keyframes fadeOut {
  0%   { opacity: 1; display: block; }
  99%  { opacity: 0; display: block; }
  100% { opacity: 0; display: none; }
}
.App-logo  {
  padding: 10px 20px;  /* reduce padding */
  color: rgb(99, 97, 97);  
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  margin:0 auto;  
  opacity: 1; /* Start with 90% opacity or any desired value */
  animation: fadeOut 5s forwards 1s; /* Start fade-out after 3 seconds, fade out over 1 second */
  pointer-events: none;
  height: 20vmin;
}

.osd {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  position: absolute;
  top: 0;
  left:0;
  width: 100%;
}

.block {
  display: flex;
  align-items: center;
  padding: 12px 24px;
  background-color: #282c34a2;   
  height: 80px; 
}
.block.left {
  padding-left: 96px;
}
.block.right {
  padding-right: 96px;
}
.block.center {
  left:40vw;
  position: absolute;
  top:0
}
.epg{
  background:#282c34a2;
  border-radius:12px;
  backdrop-filter: blur(2px);
}
.epg table {
  /* font-family: "Fraunces", serif;
  font-size: 125%; */
  white-space: nowrap;
  margin: 0;
  border: none;
  border-collapse: separate;
  border-spacing: 0;
  table-layout: fixed;
  /* border: 1px solid black; */
}
.epg table td,
.epg table th {
  /* border: 1px solid black; */
  padding: 0.5rem 1rem;
}
.epg table thead tr{
   /* background:#3a3f47; */
   /* position: absolute; */
   /* top: 0;    */
}
.epg table thead th {
  padding: 0px;
  margin: 0px;
  position: sticky;
  top: 0;
  z-index: 1;
  width: 25vw;  
  background:#3a3f47;
}
.epg table thead th div{
  /* z-index: 2; */
  /* background:#3a3f47; */
  padding: 10px 0px;
  margin: 0px;
  min-width: 3rem;
}
.epg table td {
  /* background: #fff; */
  /* padding: 4px 5px; */
  padding: 0;
  text-align: center;
}
.epg table tbody td:not(:empty) {
  background:#404070;
  width:100%;    
  white-space:normal;
  word-wrap: break-word;  
  border:1px solid #000;
  padding:0 2px;
}
.epg table tbody th {
  font-weight: 100;
  font-style: italic;
  text-align: left;
  position: relative;
}
.epg table thead th:first-child {
  position: sticky;
  left: 0;
  z-index: 2;
}
.epg table tbody th {
  position: sticky;
  left: 0;
  background: #3a3f47; 
  z-index: 1;
}
